body {
  font-family: "Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}

header.main {
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
  border-bottom: 3px solid #eee;
  position: initial;

  h1 {
    margin-left: 0;
    padding-left: 0;

    &::before {
      content: none;
    }
  }
}

.d-print-none {
  display: none;
}

.section {
  article {
    &.summary.summary-print {
      margin-bottom: 1rem;

      header {
        h1, h2 {
          font-size: 1rem;
          font-weight: normal;
          display: inline;
        }

        h1 {
          font-weight: bold;
        }

        h2 {
          &:before {
            content: " â€” "
          }

          strong {
            display: none;
          }
        }
      }
    }
  }
}

.section-main {
  margin-bottom: inherit;
}
